function Loader() {
  return (
    <div className="loader">
      <img
        className="loader-img"
        src={process.env.PUBLIC_URL + "/favicon.ico"}
        alt="icon"
      ></img>
      <div className="loader-text">사용자 정보를 불러오는 중</div>
    </div>
  );
}

export default Loader;
